<template>
  <div>
    <div :style="{height: specHeight + 'px'}" style="width: 100%;"></div>
    <mt-header style="padding: 0px;" title="车辆监控">
      <router-link to="/" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
    </mt-header>
    <div @click="clickHandle">
      <mt-cell is-link>
        <span slot="title">{{projname}}</span>
      </mt-cell>
    </div>
    <el-table
      :data="tableData"
      border
      cell-style="padding: 0"
      :header-cell-style="tableHeaderColor"
      style="width: 100%">
      <el-table-column
        prop="CPH"
        fixed
        label="车牌号"
        width="100">
      </el-table-column>
      <el-table-column
        prop="DeptName"
        label="部门名称"
        width="100">
      </el-table-column>
      <el-table-column
        prop="UserName"
        label="人员姓名"
        width="100">
      </el-table-column>
      <el-table-column
        prop="InTime"
        label="入场时间"
        width="100">
      </el-table-column>
      <el-table-column
        prop="OutTime"
        label="出场时间"
        width="100">
      </el-table-column>
    </el-table>
    <mt-popup style="width: 100%" v-model="sheetVisible" position="bottom" class="mint-popup">
      <mt-picker style="width: 100%" :slots="projData" value-key="ProjectName" @change="onValuesChange"></mt-picker>
    </mt-popup>
  </div>
</template>
<script>
export default {
  computed: {
    projData () {
      let projSlot = [{
        flex: 1,
        textAlign: 'center',
        values: this.$store.getters.projName,
        className: 'slot1'
      }]
      return projSlot
    },
    tableData () {
      let realData = this.$store.getters.carInfoList[this.projKey]
      return realData
    }
  },
  data () {
    return {
      projKey: this.$store.getters.projName[0].ProjectNO.toString(),
      specHeight: this.$store.getters.statusBarHeight,
      popupVisible: false,
      sheetVisible: false,
      projname: this.$store.getters.projName[0].ProjectName
    }
  },
  methods: {
    tableHeaderColor ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        return 'background-color: #F7F6Fd;color: #B6B5C2;font-weight: 500;padding: 1px;'
      }
    },
    onValuesChange (picker, values) {
      if (values[0] > values[1]) {
        picker.setSlotValue(1, values[0].ProjectName)
      }
      console.log(picker.getValues())
      this.projname = picker.getValues()[0].ProjectName
      this.projKey = picker.getValues()[0].ProjectNO.toString()
    },
    clickHandle () {
      console.log('11111111111111')
      this.sheetVisible = !this.sheetVisible
    }
  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>

</style>



